$pwa-primary-color: #0274BE;
$pwa-border-color: #eeeeee;
$pwa-font-size: 16px;
$pwa-line-height: 18px;

#punctuation-checker {
  @import "./component/loader";

  box-sizing: border-box;
  margin-top: 30px;

  * {
    box-sizing: border-box;
  }

  .editor {
    position: relative;
    width: 100%;
    min-height: 300px;
    border: 1px solid #ced4da;
    padding: 10px 15px;
    margin-bottom: 1rem;
    cursor:text;

    &[data-placeholder].empty:before {
      content: attr(data-placeholder);
      color: gray;
      font-style:italic;
    }

    &:after {
      content: attr(data-error);
      position: absolute;
      bottom: 0;
      left: -1px;
      right: -1px;
      padding: 5px 15px;
      background: lighten(#FA5A4E, 15);
      text-align: center;

      opacity: 0;
      -webkit-transition:   opacity 0.5s ease;
      -moz-transition:  opacity 0.5s ease;
      -ms-transition:  opacity 0.5s ease;
      -o-transition:  opacity 0.5s ease;
      transition:  opacity 0.5s ease;
      transition-delay:0.2s;
    }
    &.has-error:after {
      opacity: 1;
    }

    .pwa[data-report] {
      animation: none;
      box-shadow: inset 0 0px 0 white, inset 0 -2px 0 #FA5A4E;
    }
    .pwa[data-report][data-category=grammarspelling] {
      animation: none;
      box-shadow: inset 0 0px 0 white, inset 0 -2px 0 #FA5A4E;
    }

    .pwa[data-report][data-category=grammargrammar] {
      animation: none;
      box-shadow: inset 0 0px 0 white, inset 0 -2px 0 #56A8FC;
    }

    .pwa[data-report][data-category=grammarstyle] {
      animation: none;
      box-shadow: inset 0 0px 0 white, inset 0 -2px 0 #fad163;
    }
  }

  .controls:after {
    content: "";
    display: table;
    clear: both;
  }

  .language-wrapper {
    float: left;
    margin-bottom: 1rem;

    label {
      float: left;
      line-height: calc(2.25rem + 2px);
      margin-right: 0.5rem;
      display: inline-block;
    }
    select {
      width: auto;
      min-width: 200px;

      // bootstrap style
      display: block;
      height: calc(2.25rem + 2px);
      padding: .375rem .75rem;
      font-size: 1rem;
      line-height: 1.5;
      color: #495057;
      background-color: #fff;
      background-clip: padding-box;
      border: 1px solid #ced4da;
      border-radius: .25rem;
      transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    }
  }

  button.check {
    float: right;
    color: white;
    background: $pwa-primary-color;

    // bootstrap style
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  }

  [data-popup-correction] {
    user-select: none;
    position: fixed;
    min-width: 250px;
    max-width: 500px;
    padding: 5px;
    background: transparent;
    visibility: hidden;
    pointer-events: none;
    z-index: 2147483647;
    opacity: 0;

    &.visible {
      pointer-events: auto;
      visibility: visible;
      opacity: 1;
    }

    .pwa-popup-correction-inner {
      border-radius: 4px;
      box-shadow: 0 2px 6px 3px rgba(90, 94, 94, 0.2);
      border: 1px solid $pwa-border-color;
      background: white;
      padding: 10px 15px;
    }

    .pwa-popup-header {
      display: block;
      font-size: $pwa-font-size;
      line-height: $pwa-line-height;
      margin-bottom: 10px;
      padding-right: 25px;
      word-break: break-word;
    }

    .pwa-correction-item {
      padding: 8px 0;
      font-size: $pwa-font-size;
      line-height: $pwa-line-height;
      color: $pwa-primary-color;

      &:before {
        content: "";
        display: inline-block;
        width: 35px;
        height: 9px;
        line-height: $pwa-font-size;
        vertical-align: middle;
        background: url('data:image/svg+xml;base64,');
        margin: -2px 10px 0;
        background-size: 35px 9px;
      }

      &:hover {
        background: lighten($pwa-primary-color, 55);
      }
    }

    .pwa-popup-footer {
      display: inline-block;
      padding: 10px 0 0 0;
      width: 100%;
      box-sizing: border-box;
      position: relative;
      white-space: nowrap;

      &:before {
        display: block;
        content: "";
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-top: 1px solid $pwa-border-color;
      }

      .pwa-popup-footer-button {
        color: $pwa-primary-color;

        &.pwa-ignore-button:before {
          content: "";
          display: inline-block;
          opacity: 0.5;
          background: url('data:image/svg+xml;base64,');
          background-size: 18px $pwa-font-size;
          vertical-align: text-bottom;
          width: 18px;
          height: $pwa-font-size;
          margin-right: 6px;
        }
      }
    }
  }

  @media all and (max-width: 700px) {
    .language-wrapper {
      float: none;
      width: 100%;

      label {
        float: none;
      }

      select {
        width: 100%;
      }
    }

    button.check {
      float: left;
    }
  }
}




